'use client'

import { useState } from 'react'
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import L from 'leaflet'

// Fix default icon issue with Leaflet in React
delete (L.Icon.Default.prototype as any)._getIconUrl
L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-2x.png',
  iconUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon.png',
  shadowUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
})

// Create custom orange icon for clubs and events
const orangeIcon = new L.Icon({
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-orange.png',
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-orange-2x.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
})

interface Pin {
  id: string
  type: 'club' | 'event' | 'friend'
  position: [number, number]
  title: string
  description?: string
}

const samplePins: Pin[] = [
  {
    id: '1',
    type: 'club',
    position: [51.505, -0.09],
    title: 'Club Alpha',
    description: 'Popular club with great music',
  },
  {
    id: '2',
    type: 'event',
    position: [51.51, -0.1],
    title: 'Party Night',
    description: 'Dance all night long',
  },
  {
    id: '3',
    type: 'friend',
    position: [51.507, -0.08],
    title: 'Friend: Alice',
  },
]

export default function MapNoSSR() {
  const [pins, setPins] = useState<Pin[]>(samplePins)
  const [filterType, setFilterType] = useState<string>('all')

  const filteredPins =
    filterType === 'all' ? pins : pins.filter((pin) => pin.type === filterType)

  return (
    <div className="w-full h-[600px] rounded-lg overflow-hidden shadow-lg">
      <div className="p-4 flex gap-4 bg-white rounded-t-lg">
        <select
          value={filterType}
          onChange={(e) => setFilterType(e.target.value)}
          className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          aria-label="Filter pins by type"
        >
          <option value="all">All</option>
          <option value="club">Clubs</option>
          <option value="event">Events</option>
          <option value="friend">Friends</option>
        </select>
      </div>
      <MapContainer
        center={[51.0, 4.04]}
        zoom={13}
        scrollWheelZoom={true}
        className="w-full h-[540px]"
      >
        <TileLayer
          attribution='&copy; <a href="https://osm.org/copyright">OpenStreetMap</a>'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {filteredPins.map((pin) => (
          <Marker key={pin.id} position={pin.position}>
            <Popup>
              <strong>{pin.title}</strong>
              {pin.description && <p>{pin.description}</p>}
            </Popup>
          </Marker>
        ))}
      </MapContainer>
    </div>
  )
}
